.title{
  position:relative;
  top:20px;
  padding-bottom:50px;
  color: black;
  line-height: 40px;
}
.title-content{
  display: block;
  opacity: 0;
  animation: revealText .5s forwards;
}
.title-second{
  animation-delay: .3s;
}
.title-third{
  animation-delay: .6s;
}
.title-fourth{
  animation-delay: .9s;
}
.title-fifth{
  animation-delay:1.2s;
}
@keyframes revealText{
  from{
    transform: translateY(20px);
  }
  to{
    opacity: 1;
    transform: none;
  }
}
body{
  margin-left:6%;
  margin-right:6%;
  margin-top:50px;
  background-color: white;
}
*{
  font-family: ryo-text-plusn, menlo;
  font-weight: 500;
  font-style: normal;
}

/*animation*/

.one {
  opacity: 0;
  -webkit-animation: dot 1.3s infinite;
  -webkit-animation-delay: 0.0s;
  animation: dot 1.3s infinite;
  animation-delay: 0.0s;
}

.two {
  opacity: 0;
  -webkit-animation: dot 1.3s infinite;
  -webkit-animation-delay: 0.2s;
    animation: dot 1.3s infinite;
    animation-delay: 0.2s;
}

.three {
  opacity: 0;
  -webkit-animation: dot 1.3s infinite;
  -webkit-animation-delay: 0.3s;
   animation: dot 1.3s infinite;
   animation-delay: 0.3s;
}

@-webkit-keyframes dot {
    0% { opacity: 0; }
   50% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes dot {
    0% { opacity: 0; }
   50% { opacity: 0; }
  100% { opacity: 1; }
}



